import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);  //注册插件
 
export default  new Vuex.Store({
  state: {
    navbar:0,
    navSwiper:0
  },
  //唯一改变state的方法
  mutations: {
    increment (state,pyload) {
      state.navSwiper = pyload.amount
    }
  }
})


