<template>
  <div id="app">
        <div id="nav" class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                    <router-link to="Model3D-list">3D</router-link>
                  </div>
                  <div class="swiper-slide">
                    <router-link to="error-list">数学</router-link>
                  </div>
                  <div class="swiper-slide">
                    <router-link to="error-list">Data</router-link>
                  </div>
                  <div class="swiper-slide">
                    <router-link to="error-list">联系</router-link>
                  </div>
                  <div class="swiper-slide">
                    <router-link to="error-list">404</router-link>
                  </div>
                  <div class="swiper-slide">
                    <router-link to="error-list">404</router-link>
                  </div>
                  <div class="swiper-slide">
                  <router-link to="error-list">404</router-link>
                  </div>
                  <div class="swiper-slide">
                  <router-link to="error-list">404</router-link>
                  </div>
                  <div class="swiper-slide">
                  <router-link to="error-list">404</router-link>
                  </div>
            </div>          
        </div>
        <div id="Container" class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                <router-view></router-view>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
export default {
  name: 'app',
  data(){
    return {
      swiper:Swiper,
      swipernav:null,
      swiperpage:null,
      swiperscroll:null
    }
  },
  watch:{

  },
  mounted(){
      this.swipernav = new this.swiper('#nav',{
            slidesPerView: 6,
            freeMode: true,

        }),
      this.swiperpage = new this.swiper('#Container',{
            watchSlidesProgress: true,
            resistanceRatio: 0,
      })
  },
  methods:{

  },
  components: {

  }
}
</script>

<style>
html,body,#app,#main,#Container{
  height:100%;
  margin: 0;
  padding:0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#nav{
  margin-top:20px;
  text-align: center;
  border-bottom:1px solid #dedede;
}
#nav a{
  text-decoration: none;
}
</style>



