import Vue from 'vue'
import Router from 'vue-router'
import {phonepage} from '../components/phonepage'

Vue.use(Router)
var routes  = phonepage.map(page=>{
	const name = page.name;
	return{
		path:`/${name}`,
		name,
		component:page
	}
})
routes.push( {
    path: '*',
    redirect: '/Model3D-list'
} )
export default new Router({ routes })



